.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    background-color: rgba(0,0,0, 0.3);
}
.popup-inner {
    position: absolute;
    left: 37%;
    right: 37%;
    top: 35%;
    bottom: 25%;
    margin: auto;
    color: white;
    background-color: rgba(0,0,0, 0.7);
}
.close-button {
    border-radius: 50%;
    float: left;
}
.popup-update {
    
    border-radius: 5px;
}
td, th {
